<template>
  <div class="sideBar">
    <div class="menu-wrapper" v-for="(item, index) in menuList" :key="index">
      <div v-if="item.children">
        <div
          class="menu"
          :class="select === index ? 'menuselect' : ''"
          @click="menuClick(index)"
        >
          {{ item.name }}
          <img
            class="left-arrow"
            :class="select === index ? 'down-arrow' : ''"
            src="../../assets/left.png"
            alt=""
          />
        </div>
        <div class="fold">
          <div
            class="submenu"
            v-for="i in item.children"
            :key="i.name"
            @click="toPage(i.path)"
          >
            {{ i.name }}
          </div>
        </div>
      </div>
      <div v-else class="menu" @click="toPage(item.path)">
        <div>{{ item.name }}</div>
        <div class="fold" style="display: none"></div>
      </div>
    </div>
  </div>
</template>
<script>
import getMenu from '../../../mock/menu/genMenu'
export default {
  name: 'sideBar',
  data() {
    return {
      menuList: getMenu('admin').data,
      select: false
    }
  },
  mounted() {},
  methods: {
    menuClick(index) {
      if (this.select === index) {
        document.getElementsByClassName('fold')[this.select].style.height =
          '0px'
        this.select = false
      } else {
        if (this.select !== false) {
          document.getElementsByClassName('fold')[this.select].style.height =
            '0px'
        }
        let subNum = this.menuList[index].children.length
        this.select = index
        document.getElementsByClassName('fold')[index].style.height = `${
          subNum * 45
        }px`
      }
    },
    toPage(path) {
      this.$router.push(path)
    }
  }
}
</script>
<style lang="scss" scoped>
.sideBar {
  z-index: 90;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  padding: 10px;
  width: 200px;
  overflow: auto;
  background: linear-gradient(#6984eb, #7fb2eb, #02687e);
  box-sizing: border-box;
}
.menu {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 50px;
  padding: 10px;
  box-sizing: border-box;
  background: transparent;
  font-family: sans-serif;
  color: rgb(255, 255, 255, 0.85);
  transition: all 0.3s ease-out;
}
.menu-wrapper + .menu-wrapper {
  margin-top: 5px;
}
.submenu {
  display: flex;
  align-items: center;
  padding: 10px 10px 10px 30px;
  font-family: sans-serif;
  height: 40px;
  box-sizing: border-box;
  color: rgb(255, 255, 255, 0.85);
  transition: all 0.3s ease-out;
}
.menu:hover {
  background: rgb(255, 255, 255, 0.24);
}
.submenu:hover {
  background: rgb(255, 255, 255, 0.24);
}
.fold {
  height: 0;
  overflow: hidden;
  transition: all 0.3s ease-out;
  background: rgb(255, 255, 255, 0.1);
}
.fold .submenu {
  margin: 5px 0;
}
.menuselect {
  background: rgb(255, 255, 255, 0.24);
}
.left-arrow {
  height: 12px;
  transition: all 0.3s ease-out;
}
.down-arrow {
  transform: rotate(-90deg);
}
</style>