import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Home",
    component: Home,
    redirect: { name: "bibibi" },
    children: [
      {
        path: "linkPage",
        name: "linkPage",
        component: () => import("../views/About.vue"),
      },
      {
        path: "/vueNote",
        name: "vueNote",
        // route level code-splitting
        // this generates a separate chunk (about.[hash].js) for this route
        // which is lazy-loaded when the route is visited.
        component: () =>
          import(
            /* webpackChunkName: "about" */ "../views/noteManagement/vueNote.vue"
          ),
      },
      {
        path: "/css",
        name: "css",
        component: () => import("../views/noteManagement/chirsmasTree.vue"),
      },
      {
        path: "/js",
        name: "js",
        component: () => import("../views/noteManagement/question.vue"),
      },
      {
        path: "/bfc",
        name: "bfc",
        component: () => import("../views/noteManagement/bfcTest.vue"),
      },
      {
        path: "/learn",
        name: "learn",
        component: () => import("../views/assignment/learn/index.vue"),
      },
      {
        path: "/others",
        name: "others",
        component: () => import("../views/assignment/others/index.vue"),
      },
      {
        path: "/lantern",
        name: "lantern",
        component: () => import("../views/cssSeries/lantern.vue"),
      },
      {
        path: "/practice",
        name: "practice",
        component: () => import("../views/cssSeries/practice.vue"),
      },
      {
        path: "/formilyOne",
        name: "formilyOne",
        component: () => import("../views/formily/testOne.vue"),
      },
      {
        path: "/photoShop",
        name: "photoShop",
        component: () => import("../views/photoShop/index.vue"),
      },
    ],
  },
  {
    path: "/bibibi",
    name: "bibibi",
    component: () => import("../views/bibibi/index.vue"),
  },
];
const originalPush = VueRouter.prototype.push;
VueRouter.prototype.push = function push(location) {
  return originalPush.call(this, location).catch((err) => err);
};
const router = new VueRouter({
  routes,
});

export default router;
